body::-webkit-scrollbar {
    width: 6px;
    background-color: #fff;
}

body::-webkit-scrollbar-thumb {
    background-color: #222;
}

body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: inherit;
    font-size: inherit;
    font-weight: inherit;
}

.btn-green {
    background: var(--colormain);
    color: #fff;
    transition: 0.3s all;

    &:hover {
        background: #71a21d;
        color: #fff;
    }
}

.uppercase {
    text-transform: uppercase;
}

.medium {
    font-weight: 500;
}

.bold {
    font-weight: bold;
}

.hover-tooltip {
    position: relative;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    .tooltip-s {
        position: absolute;
        left: -60px;
        opacity: 0;
        -webkit-transition: .3s ease-in;
        transition: .3s ease-in;
        visibility: hidden;
        background: #2c2f36;
        color: #fff;
        width: 150px;
        text-align: center;
        border-radius: 4px;
        padding: 5px 5px;
        top: 20px;
        font-size: 13px;

        &::before {
            content: "";
            border-left: 5px solid rgba(255, 255, 255, 0);
            border-right: 5px solid rgba(255, 255, 255, 0);
            border-bottom: 6px solid #2c2f36;
            position: absolute;
            top: -6px;
            left: 49%;
        }
    }
    .iconSave{
        height: 40px;
        width: 40px;
        border-radius: 40px;
        display: inline-block;
        margin-right: 0;
        margin-top: 0 !important;
        cursor: pointer;
        background: url(images/icons/ic_save.svg) no-repeat center center;
        &.active{
            background: url(images/icons/ic_unsave.svg) no-repeat center center;
        }
    }
    &:hover {
        background: #e5e5e5;

        .tooltip-s {
            top: 50px;
            visibility: visible;
            opacity: 1;
        }
    }
}

.home-small-more {
    color: #055699;
    line-height: 16px;

    img {
        width: 11px;
        vertical-align: initial;
    }
}


.thumb_img,
.thumb {
    display: block;
    overflow: hidden;
    height: 1px;
    position: relative;
    width: 100%;
}

.thumb_img,
.thumb_video,
.thumb_16x9 {

    video,
    img,
    iframe {
        -o-object-position: top;
        object-position: top;
        -o-object-fit: cover;
        object-fit: cover;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
    }
}



.thumb_5x3,
.thumb-5x3 {
    padding-bottom: 60%;
}

.thumb_3x4 {
    padding-bottom: 133.33%;
}

.thumb_2x3,
.thumb-2x3 {
    padding-bottom: 150%;
}

.thumb_5x5,
.thumb-5x5 {
    padding-bottom: 100%;
}

.thumb_16x9,
.thumb_video {
    padding-bottom: 56.25%;
}

.title-cate {
    font: bold 28px var(--font1);
    margin-bottom: 20px;
}
.tab-new{
    margin-bottom: 20px;
    a{
        color: #055699;
    }
    span{
        margin: 0 5px;
    }
}
.art_item {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 24px;
    width: 100%;

    .thumb_art {
        position: relative;
        width: 140px;
        .icon{
            position: absolute;
            color: #fff;
            right: 10px;
            bottom: 10px;
            z-index: 2;
            .icon-svg{
                color: #fff;
            }
        }
    }

    .content {
        width: calc(100% - 140px);
        padding-left: 15px;

        .title_news {
            font: bold 16px/1.4 var(--font1);
            color: #2d373f;
            margin-bottom: 8px;

            svg {
                color: #2D8B5A;
            }
        }

        .description {
            font: 400 14px arial;
            margin-bottom: 8px;
            line-height: $lhbody;
            color: #4F4F4F;

            a {
                color: #4F4F4F;
            }
        }

        .status {
            color: #757575;
            align-items: center;
            font-size: 14px;
            .itemt {
                display: flex;
                align-items: center;

                svg {
                    width: 14px;
                    height: 14px;
                    margin-right: 4px;
                }
            }

            .line {
                margin: 0 13px;
                background: #E9D7CA;
                font-size: 0;
                height: 15px;
                width: 1px;
            }
        }

        .btn-flex {
            display: flex;

            .btn {
                margin-right: 10px;
                border-radius: 96px;
                border: 1px solid #2D8B5A;
                color: #2D8B5A;
                font-size: 14px;
                line-height: 38px;
                padding: 0 12px;

                a {
                    color: #2D8B5A;
                    display: flex;
                    align-items: center;

                    svg {
                        margin-right: 4px;
                    }
                }
            }

            .view {
                background: #359D67;
                color: #fff;

                a {
                    color: #fff;

                    svg {
                        margin-right: 4px;
                    }
                }

                &:hover {
                    background: #2D8B5A !important;


                }

                &:active {
                    border: 1px solid #1B5A39 !important;
                    background: #1B5A39 !important;
                    color: #fff;

                    a {
                        color: #fff;
                    }
                }
            }
        }

        .social {
            display: flex;

            a {
                background: #F2F2F3;
                border-radius: 50%;
                width: 40px;
                height: 40px;
                text-align: center;
                line-height: 38px;
                color: #757575;
                margin-right: 8px;

                &:hover {
                    background: #E5E5E5;
                }

                &:active {
                    background: #D4D4D4;
                }

                svg {
                    width: 14px;
                    height: 14px;
                }
            }
        }

    }
    .content-article{
        display: flex;
        flex-wrap: wrap;
        span{
            font-weight: bold;
            font-size: 30px;
            line-height: 41px;
            text-transform: capitalize;
            color: #055699;
            width: 52px;
            display: inline-block;
        }
        h3{
            font-weight: 600;
            font-size: 16px;
            width: calc(100% - 52px);
            margin-bottom: 0;
        }
    }
    .hover-tooltip .tooltip-s{
        width: 120px;
        left: -45px;
    }
    &.thumb-full {
        display: block;

        .thumb_art {
            width: 100%;
            margin: 0 0 10px 0;
            float: none;
        }

        .content {
            width: 100%;
            padding-left: 0;
        }
    }
}
.swiper-container{
    .art_item{
        margin-bottom: 0;
    }
}

.bg-agray{
    background: #f0f2f5;
    padding: 30px 0;
}

.load-more{
    border: 1px solid #055699;
    box-sizing: border-box;
    border-radius: 5px;
    padding: 9px 20px;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: #055699;
    img{
        margin-left: 5px;
    }
    &.active{
        img{
            display: none;
        }
    }
}


[class*='grid__'] {
    column-gap: 30px;
    row-gap: 30px;
    position: relative;

    .art_item {
        margin-bottom: 0;
    }
}

.grid {
    display: grid;
    width: 100%;

    &__1 {
        grid-template-columns: repeat(1, 1fr);
    }

    &__2 {
        grid-template-columns: repeat(2, 1fr);
    }

    &__3 {
        grid-template-columns: repeat(3, 1fr);
    }

    &__4 {
        grid-template-columns: repeat(4, 1fr);
    }

    &__5 {
        grid-template-columns: repeat(5, 1fr);
    }

    &__6 {
        grid-template-columns: repeat(6, 1fr);
    }

    &__7 {
        grid-template-columns: repeat(7, 1fr);
        column-gap: 8px;
        row-gap: 8px;
    }
}